// Servio especializado para pesquisas tcnicas de produtos
import { GoogleGenerativeAI } from '@google/generative-ai';

const API_KEY = 'AIzaSyAf-Oe56q4Rao0OodEOtnEjtI_FpOmDg6I';

class PesquisaService {
  constructor() {
    this.genAI = new GoogleGenerativeAI(API_KEY);
    this.model = this.genAI.getGenerativeModel({ model: 'gemini-1.5-flash' });
  }

  // Categorias de busca disponveis
  getCategoriasBusca() {
    return [
      {
        id: 'manuais',
        nome: 'Manuais do Fabricante',
        descricao: 'Manuais tcnicos e de instrues do fabricante',
        fontes: ['Sites oficiais dos fabricantes', 'Portais tcnicos', 'Documentao oficial']
      },
      {
        id: 'laudos',
        nome: 'Laudos e Testes',
        descricao: 'Laudos tcnicos e resultados de testes de funcionalidade',
        fontes: ['Laboratrios certificados', 'Institutos de pesquisa', 'rgos reguladores']
      },
      {
        id: 'normas',
        nome: 'Normas Tcnicas',
        descricao: 'Normas ABNT, NR e outras regulamentaes tcnicas',
        fontes: ['ABNT', 'Ministrio do Trabalho', 'rgos reguladores setoriais']
      },
      {
        id: 'anvisa',
        nome: 'Informaes ANVISA',
        descricao: 'Registros, alertas e informaes da ANVISA',
        fontes: ['Portal ANVISA', 'Sistema de notificaes', 'Boletins tcnicos']
      },
      {
        id: 'vigilancia',
        nome: 'Vigilncia Sanitria',
        descricao: 'Informaes de vigilncias sanitrias estaduais e municipais',
        fontes: ['rgos estaduais', 'Vigilncias municipais', 'Sistemas de notificao']
      },
      {
        id: 'reclamacoes',
        nome: 'Reclamaes de Usurios',
        descricao: 'Reclamaes e avaliaes de usurios sobre produtos',
        fontes: ['Reclame Aqui', 'Procon', 'Fruns especializados']
      },
      {
        id: 'recalls',
        nome: 'Recalls e Alertas',
        descricao: 'Recalls de produtos e alertas de segurana',
        fontes: ['Portal do Consumidor', 'INMETRO', 'Fabricantes']
      },
      {
        id: 'certificacoes',
        nome: 'Certificaes',
        descricao: 'Certificaes de qualidade e conformidade',
        fontes: ['INMETRO', 'Organismos certificadores', 'ISO']
      }
    ];
  }

  // Gerar prompt especializado para cada tipo de busca
  gerarPromptEspecializado(categoria, produto, marca, especificacoes) {
    const prompts = {
      manuais: `
        Voc  um especialista em pesquisa tcnica de produtos. Preciso encontrar informaes sobre manuais do fabricante para:
        
        Produto: ${produto}
        Marca: ${marca}
        Especificaes: ${especificacoes}
        
        Por favor, fornea informaes detalhadas sobre:
        1. Onde encontrar o manual oficial do fabricante
        2. Especificaes tcnicas principais
        3. Instrues de uso e instalao
        4. Requisitos de manuteno
        5. Garantias e condies de uso
        
        Seja especfico e cite fontes confiveis quando possvel.
      `,
      
      laudos: `
        Voc  um especialista em anlise tcnica de produtos. Preciso encontrar informaes sobre laudos e testes para:
        
        Produto: ${produto}
        Marca: ${marca}
        Especificaes: ${especificacoes}
        
        Por favor, fornea informaes sobre:
        1. Laudos tcnicos disponveis
        2. Testes de funcionalidade realizados
        3. Certificaes de qualidade
        4. Resultados de desempenho
        5. Laboratrios que realizaram os testes
        
        Priorize informaes de fontes oficiais e laboratrios certificados.
      `,
      
      normas: `
        Voc  um especialista em normas tcnicas. Preciso encontrar informaes sobre regulamentaes para:
        
        Produto: ${produto}
        Marca: ${marca}
        Especificaes: ${especificacoes}
        
        Por favor, identifique:
        1. Normas ABNT aplicveis
        2. Normas Regulamentadoras (NR) relevantes
        3. Outras regulamentaes tcnicas
        4. Requisitos de conformidade
        5. Certificaes obrigatrias
        
        Cite os nmeros das normas e rgos responsveis.
      `,
      
      anvisa: `
        Voc  um especialista em regulamentaes sanitrias. Preciso encontrar informaes da ANVISA sobre:
        
        Produto: ${produto}
        Marca: ${marca}
        Especificaes: ${especificacoes}
        
        Por favor, pesquise:
        1. Registro na ANVISA (se aplicvel)
        2. Alertas ou notificaes
        3. Restries de uso
        4. Classificao de risco
        5. Histrico de irregularidades
        
        Foque em informaes oficiais do portal da ANVISA.
      `,
      
      vigilancia: `
        Voc  um especialista em vigilncia sanitria. Preciso encontrar informaes sobre:
        
        Produto: ${produto}
        Marca: ${marca}
        Especificaes: ${especificacoes}
        
        Por favor, pesquise em vigilncias sanitrias:
        1. Notificaes estaduais
        2. Alertas municipais
        3. Interdies ou apreenses
        4. Irregularidades encontradas
        5. Medidas cautelares
        
        Inclua informaes de diferentes estados quando relevante.
      `,
      
      reclamacoes: `
        Voc  um especialista em anlise de satisfao do consumidor. Preciso encontrar informaes sobre reclamaes para:
        
        Produto: ${produto}
        Marca: ${marca}
        Especificaes: ${especificacoes}
        
        Por favor, analise:
        1. Principais reclamaes dos usurios
        2. Problemas mais frequentes
        3. Avaliaes e notas
        4. Resolues de problemas
        5. Tendncias de satisfao
        
        Use fontes como Reclame Aqui, Procon e fruns especializados.
      `,
      
      recalls: `
        Voc  um especialista em segurana de produtos. Preciso encontrar informaes sobre recalls para:
        
        Produto: ${produto}
        Marca: ${marca}
        Especificaes: ${especificacoes}
        
        Por favor, pesquise:
        1. Recalls oficiais
        2. Alertas de segurana
        3. Defeitos identificados
        4. Medidas corretivas
        5. Cronograma de correes
        
        Priorize informaes do Portal do Consumidor e INMETRO.
      `,
      
      certificacoes: `
        Voc  um especialista em certificaes de qualidade. Preciso encontrar informaes sobre certificaes para:
        
        Produto: ${produto}
        Marca: ${marca}
        Especificaes: ${especificacoes}
        
        Por favor, identifique:
        1. Certificaes de qualidade
        2. Selos de conformidade
        3. Organismos certificadores
        4. Validade das certificaes
        5. Processos de certificao
        
        Foque em certificaes oficiais e reconhecidas.
      `
    };
    
    return prompts[categoria] || prompts.manuais;
  }

  // Realizar busca especializada
  async buscarInformacoesTecnicas(categoria, produto, marca, especificacoes = '') {
    try {
      console.log(` PesquisaService: Iniciando busca para categoria ${categoria}`);
      
      const prompt = this.gerarPromptEspecializado(categoria, produto, marca, especificacoes);
      
      console.log(` PesquisaService: Prompt gerado para ${categoria}:`, prompt.substring(0, 200) + '...');
      
      console.log(` PesquisaService: Enviando para Gemini API...`);
      
      const result = await this.model.generateContent(prompt);
      const response = await result.response;
      const text = response.text();
      
      console.log(` PesquisaService: Resposta recebida para ${categoria}:`, text.substring(0, 200) + '...');
      
      return {
        success: true,
        categoria,
        produto,
        marca,
        resultado: text,
        timestamp: new Date().toISOString()
      };
    } catch (error) {
      console.error(` PesquisaService: Erro na busca tcnica para ${categoria}:`, error);
      return {
        success: false,
        error: error.message,
        categoria,
        produto,
        marca
      };
    }
  }

  // Buscar mltiplas categorias simultaneamente
  async buscarMultiplasCategorias(categorias, produto, marca, especificacoes = '') {
    const promisses = categorias.map(categoria => 
      this.buscarInformacoesTecnicas(categoria, produto, marca, especificacoes)
    );
    
    try {
      const resultados = await Promise.all(promisses);
      return {
        success: true,
        produto,
        marca,
        resultados: resultados.filter(r => r.success),
        erros: resultados.filter(r => !r.success)
      };
    } catch (error) {
      console.error('Erro na busca mltipla:', error);
      return {
        success: false,
        error: error.message,
        produto,
        marca
      };
    }
  }

  // Gerar relatrio consolidado
  async gerarRelatorioConsolidado(produto, marca, especificacoes = '') {
    const todasCategorias = this.getCategoriasBusca().map(cat => cat.id);
    
    const resultado = await this.buscarMultiplasCategorias(
      todasCategorias, 
      produto, 
      marca, 
      especificacoes
    );
    
    if (resultado.success) {
      // Gerar prompt para consolidao
      const promptConsolidacao = `
        Voc  um especialista em anlise tcnica de produtos. Com base nas informaes coletadas sobre o produto "${produto}" da marca "${marca}", gere um relatrio consolidado que inclua:
        
        1. RESUMO EXECUTIVO
        2. ESPECIFICAES TCNICAS
        3. CONFORMIDADE REGULATRIA
        4. QUALIDADE E CERTIFICAES
        5. HISTRICO DE PROBLEMAS
        6. RECOMENDAES
        
        Informaes coletadas:
        ${resultado.resultados.map(r => `\n=== ${r.categoria.toUpperCase()} ===\n${r.resultado}`).join('\n')}
        
        Organize as informaes de forma clara e profissional, destacando pontos importantes para a pr-qualificao de bens.
      `;
      
      try {
        const consolidacao = await this.model.generateContent(promptConsolidacao);
        const response = await consolidacao.response;
        const relatorio = response.text();
        
        return {
          success: true,
          produto,
          marca,
          relatorio,
          detalhes: resultado.resultados,
          timestamp: new Date().toISOString()
        };
      } catch (error) {
        return {
          success: true,
          produto,
          marca,
          relatorio: 'Erro ao gerar relatrio consolidado',
          detalhes: resultado.resultados,
          timestamp: new Date().toISOString()
        };
      }
    }
    
    return resultado;
  }
}

export default new PesquisaService(); 